// Form Elements

input[type="text"], 
input[type="password"], 
input[type="date"], 
input[type="datetime"], 
input[type="datetime-local"], 
input[type="month"], 
input[type="week"], 
input[type="email"], 
input[type="number"], 
input[type="search"], 
input[type="tel"], 
input[type="time"], 
input[type="url"], 
input[type="color"],
textarea{
  background-color: $ghost;
  border: 1px solid $input-border-color;
  border-radius: 2px;
  height: 34px;
  padding: .7rem .9rem .6rem .7rem;
}
input{
  &#search{
    padding: .7rem 1.5rem .6rem .7rem;
  }
}

