}

//Search Icon
.fa-search{
  &:hover{
    color: $anchor-font-color;
  }
}
a > .fa-search{
  color: $input-font-color;
  &:hover{
    color: $anchor-font-color;
  }
}

//Search Form
.form-search{
  position: absolute;
  right: 0;
  width: 316px;
  > .close-btn{
    color: $input-font-color;
    margin: -20px 7px 0 0;
    right: 0;
    position: absolute;
    top: 50%;
    i{
      color: $iron;
      height: 20px;
      margin: 5px -6px 0 0;
      width: 25px;
      &:hover{
        color: $primary-color;
      }
    }
  }
}
@media only screen and (max-width: 40em) {
  .form-search{
    width: 100%;
  }
}

// Collapse Field - Store locator search component
.collapse-field{
  display: inline-block;
  
  input.button[type="submit"]{
    @include bg-gradient-color($dark-blue-color-from, $dark-blue-color-to);
    border-radius: 0 3px 3px 0;
    font-size: 14px;
    height: 34px; 
    line-height: normal;
    margin-left: -2px;
    text-transform: uppercase;

    &:hover{
	  @include bg-gradient-color( darken($dark-blue-color-from, 6%), darken($dark-blue-color-to, 6%) );
    }
  }
} 

//Label
label{
  font-size: $base-font-size - 2;
}

// Google recaptcha 
.g-recaptcha, .recaptcha {
	padding: 10px 0 20px 0;
	
	border-radius: 4px;
border-right: 1px solid #d8d8d8;
overflow: hidden;
	background-color: #00F;
}
#g-recaptcha {
    transform: scale(1.42);
    transform-origin: 0 0 0;
}


// autocomplete

.ui-autocomplete .ui-menu-item {
	line-height: 25px;
	padding: 1px 8px;
	cursor: pointer;
}
.ui-widget-content .ui-state-focus {
	border-width: 1px 0;
	padding: 0 8px;
}


input.hidden-input {
	display: block;
	opacity: 0;
	height: 0;
	padding: 0;
	margin: 0;
}


